

* {
	font-weight: bold;
	font-family: Calibri, serif;
}

.game {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	border: 3px solid;
	min-height: 250px;
}

.game-header {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: space-evenly;
	        justify-content: space-evenly;
}

.game-header div{
	width: 33%;
	text-align: center;
	border: 1px solid black;
	border-radius: 10px;
	padding: 10px;
	margin: 5px;
	background-color: rgba(255, 255, 255, .4);
	color: black;
	font-size: 20px;
}

.game-content{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.game-info{
	font-size: 7vw;
}

.game-info .failure{
	color: red;
}

.initial {
	background-color: gray;
	border-color: black;
}

.running {
	background-color: lightblue;
	border-color: blue;
}

.failure {
	background-color: #ed969e;
	border-color: red;
}

.pause {
	background-color: lightgoldenrodyellow;
	border-color: orange;
}
.menu {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	width: 100%;
}

.menu-item {
	width: 50%;
	border: 1px solid black;
	margin: 5px;
	padding: 20px 50px;
	text-align: center;
	cursor: pointer;
	background-color: rgba(0, 0, 0, .4);
	color: white;
}

.menu-item:hover {
	opacity: 0.5;
}
.question {
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.question-text {
	font-size: 50px;
	border: 1px solid white;
	background-color: white;
	width: 50%;
	text-align: center;
	padding: 20px;
	margin: 10px;
}

.question-choices {
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.question-choices div {
	border: 1px solid black;
	border-radius: 40px;
	width: 40%;
	text-align: center;
	margin: 5px;
	padding: 20px;
	font-size: 35px;
	cursor: pointer;
	background-color: rgba(255, 255, 255, .4);
}

.question-choices div:hover{
	opacity: 0.6;
}
